/**
*
* tilted-section
*
**/
.tilted-sections-wrap {
  overflow: hidden;
}
.tilted-section {
  display: block;
  position: relative;
}
.tilted-section__tilt {
  display: block;
  position: absolute;
  top: -84px;
  left: 50%;
  margin-left: -960px;
  width: 0;
  height: 84px;
  border-style: solid;
  border-width: 84px 0 0 1920px;
}
.tilted-section__tilt--steep {
  border-width: 126px 0 0 1920px;
  top: -126px;
  height: 126px;
}
.tilted-section--up .tilted-section__tilt {
  left: auto;
  margin-left: 0;
  right: 50%;
  margin-right: -960px;
  border-width: 0 0 84px 1920px;
}
.tilted-section--up .tilted-section__tilt--steep {
  border-width: 0 0 126px 1920px;
}
.tilted-section__tilt:before {
  content: "";
  display: block;
  height: 84px;
  top: -84px;
  left: -3920px;
  width: 2000px;
  position: absolute;
}
.tilted-section__tilt--steep:before {
  height: 126px;
  top: -126px;
}
.tilted-section--up .tilted-section__tilt:before {
  left: auto;
  right: -2000px;
  top: 0;
}
.tilted-section--bottom {
  padding-bottom: 84px;
}
.tilted-section--steep-bottom {
  padding-bottom: 126px;
}
.tilted-section--blue {
  background-color: #043bff;
}
.tilted-section--blue .tilted-section__tilt {
  border-color: transparent transparent transparent #043bff;
}
.tilted-section--blue.tilted-section--up .tilted-section__tilt {
  border-color: transparent transparent #043bff transparent;
}
.tilted-section--blue .tilted-section__tilt:before {
  background-color: #043bff;
}
.tilted-section--dark {
  background-color: #272c2e;
}
.tilted-section--dark .tilted-section__tilt {
  border-color: transparent transparent transparent #272c2e;
}
.tilted-section--dark.tilted-section--up .tilted-section__tilt {
  border-color: transparent transparent #272c2e transparent;
}
.tilted-section--dark .tilted-section__tilt:before {
  background-color: #272c2e;
}
.tilted-section--white {
  background-color: #f9f8f3;
}
.tilted-section--white .tilted-section__tilt {
  border-color: transparent transparent transparent #f9f8f3;
}
.tilted-section--white.tilted-section--up .tilted-section__tilt {
  border-color: transparent transparent #f9f8f3 transparent;
}
.tilted-section--white .tilted-section__tilt:before {
